ave y no es posible apreciarla, una foto puede disminuir la frustración del usuario, es una referencia; también son útiles para explicar los efectos de la contaminación. ")])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img8.png"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row justify-content-end px-5",attrs:{"titulo":"¿Requiere de una frase o texto para el ejercicio de la interpretación? "}},[_c('div',{staticClass:"col-lg-8 mb-4"},[_c('p',[_vm._v("En ocasiones, el año, la ubicación o una frase ayudan a la memoria y recordación o a dejar huella en el turista. También es funcional el uso de pizarras digitales o acrílicas que permitan hacer una explicación corta con un gráfico o símbolos, deben diseñarse previamente para garantizar la claridad del mensaje. (Perales y Jiménez, 2002).")])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img9.png"),"alt":"Texto que describa la imagen"}})])])])])],1),_vm._m(20),_c('div',{staticClass:"row justify-content-center mb-4",attrs:{"data-aos":"fade"}},[_c('div',{staticClass:"col-lg-12"},[_c('div',{staticClass:"bloque-texto-g1 color-verde p-3 p-sm-4 p-md-5"},[_c('div',{staticClass:"bloque-texto-g1__img",style:({'background-image': ("url(" + (require('@/assets/curso/tema1/2.jpg')) + ")")})}),_vm._m(21)])])]),_c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"fade-right"}},[_vm._m(22),_c('div',{staticClass:"row align-items-center"},[_vm._m(23),_c('div',{staticClass:"col-lg-10 col-md-10 col-9"},[_c('p',{staticClass:"mb-4"},[_vm._v("En las actividades que el guía realiza, una de ellas es el "),_c('b',[_c('FONT',{attrs:{"COLOR":"#FD6300"}},[_vm._v(" desarrollo del sentido del gusto, donde previamente tanto la vista como el tacto analicen la forma y, mediante el olfato, se logren reconocer los elementos que se presentan en los alimentos. ")])],1),_vm._v(" También se puede incorporar el sonido que se da en dichos alimentos al ser masticados. Para que esto pueda llevarse a cabo se hace necesario hacer un ejercicio previo con todos los sentidos.")])])])]),_vm._m(24)]),_vm._m(25),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("De la siguiente manera, se comparte cómo los sentidos se logran involucrar en esas acciones que se presentan entre el guía y el turista:")]),_c('div',{staticClass:"row mb-4",attrs:{"data-aos":"fade"}},[_c('TabsB',{staticClass:"color-primario"},[_c('div',{staticClass:"py-5 py-md-5",attrs:{"titulo":"El olfato","icono":require('@/assets/curso/tema1/olfato.svg')}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-4"},[_c('b',[_vm._v("El olfato")]),_vm._v(" es el responsable de las tres cuartas partes de las emociones generadas en el turista. De hecho, estimular olfativamente a los turistas potencia su aprendizaje, porque facilita evocar recuerdos almacenados en la memoria a largo plazo. Sin embargo, el olor no es efectivo cuando se trata de recuperar información específica, porque estimula las amígdalas, responsables de la formación de las experiencias o recuerdos emocionales, influenciando directamente la toma de decisiones. ")]),_c('p',{staticClass:"mb-4"},[_vm._v("Walt Disney fue uno de los pioneros en aplicar estímulo olfativo en su parque al impregnar los espacios de atracciones con aroma a palomitas recién hechas para promover la venta de estas. Un lugar que estimule olfativamente a sus visitantes garantiza una agradable y larga permanencia, al estar atraídos por el encantador ambiente que produce el espacio perfumado, y seguramente volverán por los buenos momentos que quedaron en su memoria. Ahora bien, si la experiencia con el aroma fue positiva, los turistas recomendarán el lugar. Un ejemplo es el Hospital USP de Marbella, donde aromatizaron el área de urgencias pediátricas con madera y notas de melocotón, y comprobaron que se logró potenciar estados de ánimo positivos y la reducción del estrés y la ansiedad tanto en pacientes como en empleados. (Jiménez-Marín et al., 2019)")]),_c('p',{staticClass:"mb-4"},[_vm._v("Otro uso que puede darse en los recorridos es para proteger o revalorizar bienes o especies, como el caso del Cardo, que, por su apariencia espinosa y pequeña, rodeado por lo general de abejas, invita al visitante a sentir la fragancia de sus abundantes y discretas flores, y hará que el visitante entienda la importancia de su protección (Gallego, 2000). Igual sucede con los campos de lavanda españoles o el café en el eje cafetero colombiano.")]),_c('div',{staticClass:"cajon color-primario p-4",staticStyle:{"background-color":"#FFDF88"}},[_c('p',{staticClass:"mb-0"},[_vm._v("El olfato, por su sensibilidad, es fácil de hostigar. Investigaciones recientes han demostrado, por ejemplo, que una exposición prolongada a un olor de un alimento procesado en el establecimiento conduce a una compra menor de ese tipo de comida o rechazo de la misma. El placer olfativo es el responsable de que los olores nos resulten agradables o desagradables, y esto depende del tono, la esencia del aroma, la intensidad, el grado de concentración y la familiaridad. Por lo tanto, puede pasar que un olor agrade al 100% del grupo. (Jiménez-Marín et al., 2019).")])])])])]),_c('div',{staticClass:"py-5 py-md-5",attrs:{"titulo":"El tacto","icono":require('@/assets/curso/tema1/tacto.svg')}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-4"},[_c('b',[_vm._v("El tacto")]),_vm._v(" es el sentido háptico (tocar), comprende la percepción de estímulos mecánicos (aplicación de una fuerza sobre una superficie) que incluyen contacto, presión y golpeo sobre la piel, en la cual existen una serie de terminaciones nerviosas que hacen que los estímulos a través de la dermis sean interpretados de diversas maneras por el cerebro. (Jiménez-Marín et al., 2019). El tacto agradable libera la hormona oxitocina, vinculada con la sensación de calma y bienestar. Es importante anotar cómo el tacto debe guardar relación con lo observado. ")]),_c('div',{staticClass:"cajon color-primario p-4",staticStyle:{"background-color":"#FFDF88"}},[_c('p',{staticClass:"mb-0"},[_vm._v("También es útil para acercar al turista a distantes y profundos hábitats de difícil acceso. Con una muestra de un alga marina o la piel de un animal, es posible explicar más fácilmente las características de una especie, lo cual es menos nocivo o impactante que entrar en el hábitat real y capturar el animal o dañar la flora, al promover la experiencia turística.")])])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/5.jpg"),"alt":"Texto que describa la imagen"}})])])])]),_c('div',{staticClass:"py-5 py-md-5",attrs:{"titulo":"El oído","icono":require('@/assets/curso/tema1/oido.svg')}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-4"},[_c('b',[_vm._v("El oído")]),_vm._v(" percibe el sonido por estímulo de las áreas de la corteza cerebral y genera cambios neuroquímicos que tienen gran influencia en la conducta del usuario. Es así como la música instrumental facilita la atención y la actualización de los eventos en la memoria de largo plazo; por el contrario, la contaminación acústica que puede ser producida por música de fondo o ruidos en el ambiente puede desencadenar la necesidad de retirarse de una actividad o de un lugar, o estimular comportamiento de depresión y agresividad en el turista. (Izaguirre, 2018). ")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/6.jpg"),"alt":"Texto que describa la imagen"}})])])])]),_c('div',{staticClass:"py-5 py-md-5",attrs:{"titulo":"La audición","icono":require('@/assets/curso/tema1/audicion.svg')}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-4"},[_c('b',[_vm._v("La audición")]),_vm._v(" es trascendental en la guianza, es fundamental, mas no imposibilitante, para el servicio, por ello es importante la distinción entre oír (percibir los sonidos a través del oído) y escuchar (prestar atención a lo que se oye). La memoria auditiva es un proceso cerebral dinámico, que codifica y almacena información relacionada con las experiencias, depende del estado emocional previo del oyente, la personalidad, así como de los intereses del individuo (Jiménez-Marín et al., 2019). ")]),_c('p',{staticClass:"mb-4"},[_vm._v("Un buen recuerdo, una buena sensación, por ejemplo, en una caminata, puede generar un estímulo multisensorial para incrementar la memorabilidad del servicio al aumentar el aprendizaje; inicia cuando se invita al turista a contemplar el paisaje, motivándolo a identificar formas y colores, luego, a apreciar la sonoridad del lugar; y se cierra con aromas agradables que se perciben en el lugar.")]),_c('p',{staticClass:"mb-4"},[_vm._v("Por otro lado, también se puede usar la incomodidad sonora para el aprendizaje. Se genera, por ejemplo, al acercar el turista al oscuro y estridente mundo de los murciélagos, dominado por mitos y supersticiones; es necesaria una narración animada, soportada en imágenes o recursos audiovisuales sobre el sistema de orientación de estos animales, que no tiene nada qué envidiarle a los actuales GPS (Sistemas de Posicionamiento Global); suscitará respeto y curiosidad por la especie, desplazando el rechazo y el miedo hacia ella (Gallego, 2000).")]),_c('p',{staticClass:"mb-4"},[_vm._v("En el ámbito cultural, las bibliotecas sonoras, el uso de fragmentos de podcasts o grabaciones de otrora facilitan la evocación y son la herramienta ideal para recorridos históricos y culturales. En Colombia, entidades como el Banco de la República, la Bibl