 beneficios.")]),_c('div',{staticClass:"cajon color-primario p-4 mb-4",staticStyle:{"background-color":"#FFD667"}},[_c('p',{staticClass:"mb-0"},[_vm._v("El Internet es la herramienta de democratización del acceso de información sobre los destinos; por otro lado, al tener los usuarios acceso a equipos de mayor calidad a precios accesibles en sus viajes, sumado a las experiencias vividas pospandemia, se configura la mezcla que influye en que el turista use dispositivos como su celular para vivir la experiencia turística (Palomo y Martínez, 2018). ")])]),_c('p',{staticClass:"mb-4"},[_vm._v("En otras palabras, al portafolio de habilidades del guía se adiciona la necesidad de manejar tecnología que permita estar al tanto de las peticiones del usuario (aplicaciones para dispositivos móviles) e interactuar con el mismo (por correo electrónico, chats), así como, al mismo tiempo, usar las TIC en la mediación interpretativa, para presentar propuestas e, inclusive, materializarlas.")])]),_c('div',{staticClass:"col-lg-4 col-md-6 col-8",attrs:{"data-aos":"fade-left"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img4.png"),"alt":"alt"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-4 col-md-6 col-8",attrs:{"data-aos":"fade-right"}},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img5.png"),"alt":"alt"}})])]),_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-4"},[_vm._v("El uso de las herramientas TIC no asegura la comunicación eficiente, como tampoco asegura la experiencia, esta inicia desde que el usuario se interesa en el servicio; se pueden presentar en ese momento barreras como la frustración por un sistema de comunicación complejo, lentitud en las conexiones o por una respuesta lenta frente a inquietudes sobre detalles del servicio. Otro aspecto que incide es la baja empatía con el usuario, que se traduce en la dificultad para entender los requerimientos en el servicio; si desea evitarlo, analice las herramientas disponibles para paliar la incomunicación y, al mismo tiempo, integre el uso de herramientas alternativas para comunicar características del servicio con anterioridad y durante el mismo, y facilitar la interpretación, algunas de ellas son: podcasts, narraciones audiovisuales, escritas y orales, grabaciones, imágenes, objetos ilustrativos, pizarras, el uso de equipos electrónicos o dispositivos técnicos como altavoces, sistema de guía de viaje inalámbrico, láser para señalar, entre otros. ")]),_c('div',{staticClass:"linea mb-0"}),_c('div',{staticClass:"bloque-texto-b color-primario p-4 mb-4"},[_c('div',{staticClass:"bloque-texto-b__texto"},[_c('i',{staticClass:"fas fa-quote-left"}),_c('h4',{staticClass:"mb-0"},[_vm._v("Los expertos recomiendan una continua experimentación y prueba antes de la utilización de las TIC; si el problema persiste, revise si son los documentos o las herramientas las que no se adaptan al usuario promedio, proceda según unas pautas detalladas y prefijadas con el contratante. (Vilches y Sarmiento, 2016).")]),_c('i',{staticClass:"fas fa-quote-right"})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"cajon color-secundario p-4 mb-4",staticStyle:{"background-color":"#E8E8E8"},attrs:{"data-aos":"fade-down"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Las herramientas y equipo disponibles cambian, se modernizan continuamente, por lo tanto, el guía, de forma permanente, debe centrarse no solo en planear e implementar el uso de una herramienta confiable para su público, sino en la intencionalidad de la interpretación, que, además de brindar una información, instruya al turista. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tarjeta--con