 y la pasión, el verde y la naturaleza. (Jiménez-Marín et al., 2019). Lo anterior sustenta por qué un guía debe tener vasto conocimiento de las culturas y sobre todo la del grupo objetivo, pues en el contexto cultural y para evitar enviar mensajes equivocados con el uso del color o de una imagen, se incluye entre otros saberes como parte de la planeación y el discurso. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"fade-right"}},[_c('p',{staticClass:"mb-4"},[_vm._v("Como es comprensible, estimular la percepción visual en los turistas es importante, especialmente, el contraste de la luz que le permita la identificación de formas, la diferenciación de colores, volúmenes, situaciones, contenidos, descripciones, entre otros (Izaguirre, 2018). Por lo tanto, aunque es común que, al visitar un mirador o un lugar, la mayoría de la gente no escudriña en el paisaje, no se detiene en la búsqueda de colores, formas, siluetas, tonos, sombras, puntas, espacios, conjuntos, líneas, cortes, planos, es importante comprender o darle un significado con esa observación que, al ser profunda, es contemplativa. Para ello, es importante para el guía llevar a la observación de aquello que rodea el lugar a visitar, la naturaleza, el medio físico, las expresiones culturales y diferentes situaciones que puedan ser observadas.")]),_c('p',{staticClass:"mb-4"},[_vm._v("Respecto al uso de imágenes o ilustraciones durante el servicio guiado, el guía debe preguntarse sobre:")])]),_c('div',{staticClass:"col-lg-4 col-md-10",attrs:{"data-aos":"fade-left"}},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/vec2.svg"),"alt":"AvatarTop"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-lg-8",attr