labios y la lengua, se ponen en marcha 10.000 papilas gustativas que el ser humano tiene en la boca para evaluar y mandar información al cerebro sobre la consistencia del producto, el sabor, forma, textura y temperatura, almacenando experiencias multisensoriales poderosas para la memorabilidad, pues se puede revivir un momento solo al probar nuevamente un producto (Jiménez-Marín et al., 2019). Se considera que el estilo de vida, el nivel socioeconómico y la educación de los turistas influye significativamente en el sentido del gusto.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-2 col-md-2 col-3"},[_c('div',{staticClass:"row justify-content-end"},[_c('div',{staticClass:"col-12 col-md-12 col-lg-10"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/vec4.svg"),"alt":"Texto que describa la imagen"}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-4 col-md-6 col-8",attrs:{"data-aos":"fade-left"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/3.jpg"),"alt":"Texto que describa la imagen"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-4 col-md-6 col-8",attrs:{"data-aos":"fade-right"}},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/4.jpg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-4"},[_vm._v("Se calcula que las personas recuerdan aproximadamente el 15% de lo que prueban, por ello, es uno de los estímulos más poderosos para mostrar la gastronomía de un lugar. No obstante, la relación entre algunos sabores y sensaciones determinadas suele ser también materia de conocimiento popular, como la unión directa entre el chocolate y el placer. Estas relaciones conocidas pueden ser usadas en otro tipo de productos, por ejemplo: explicar la compleja organización militar y solar de las abejas puede ser monótona, pero si inicia con una degustación de los distintos tipos de miel según las flores o las características del contexto de la colmena, se convierte en comentarios simpáticos acerca del arduo trabajo de las otras para el disfrute de nuestro paladar. El guía habrá logrado un momento gratificante, en que la audiencia reconocerá que solo un enjambre de abejas bien organizado es capaz de producir tan agradable manjar (Gallego, 2000).")]),_c('div',{staticClass:"cajon color-secundario p-5",staticStyle:{"background-color":"#B7CEAC"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Para el uso de herramientas de estimulación al gusto, debe asegurarse de contar con las condiciones sanitarias ideales para la degustación. Se recomienda que el guía de turismo tenga conocimiento de BPM o que se asesore de un experto o la realice en un sitio especializado, para garantizar el bienestar del usuario.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center mb-4",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"col-lg-1 col-md-2 col-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/vec5.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-lg-11 col-md-10 col-9"},[_c('p',{staticClass:"mb-0"},[_vm._v("Es necesario tener presente que el cerebro está especializado en extraer generalidades de los estímulos que le llegan del entorno, a través de los sentidos, y lo aprendemos sin darnos cuenta. Esta selección de generalidades depende de las experiencias que el turista acumula a lo largo de su vida. El cerebro aprende permanentemente, las 24 horas del día, es decir, el turista aprenderá las cosas o situaciones a las que les dedica tiempo, como son los viajes.")])])])},function 