idad.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-sexto color-acento-contenido mb-3",attrs:{"data-aos":"fade"}},[_c('h5',[_vm._v("Figura 2")]),_c('span',[_vm._v("Ejemplo de secuencia para estiramiento. Fortalecimiento")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-4",attrs:{"data-aos":"fade"}},[_c('div',{staticClass:"col-lg-8"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/fig5.svg"),"alt":"Texto que describa la imagen"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg_grad-04"},[_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("La ubicación del guía frente a los usuarios y los atractivos también debe responder a una conciencia del espacio; para desarrollarla, es aconsejable adoptar técnicas corporales que aporta el teatro, como: usar una alfombra y dividirla en nueve partes iguales, ojalá cada una señalarla con un color diferente y, a partir de ello, imaginar que esta alfombra es el escenario; según la ubicación en ella, se estará más cerca o más lejos del público, de tal manera que los gestos, movimientos e incluso el tono de voz van a cambiar dependiendo de dónde se encuentre ubicado. ")]),_c('div',{staticClass:"titulo-sexto color-acento-contenido mb-5",attrs:{"data-aos":"fade"}},[_c('h5',[_vm._v("Figura 3")]),_c('span',[_vm._v("Alfombra para la práctica de desplazamiento durante la guianza")])]),_c('div',{staticClass:"row justify-content-center mb-2",attrs:{"data-aos":"fade"}},[_c('div',{staticClass:"col-lg-12"},[_c('p',{staticClass:"text-center"},[_c('b',[_vm._v("Arrioba lejos del público")])])])]),_c('div',{staticClass:"row justify-content-center mb-4",attrs:{"data-aos":"fade"}},[_c('div',{staticClass:"col-lg-6"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/fig6.svg"),"alt"